<template>
    <div id="contact">
        <div class="card text-dark bg-light">
            <img src="../../assets/pizza2.jpg" alt="" class="card-img-bottom">
            <div class="card-header">
                <h2>联系我们</h2>
            </div>
            <div class="card-body">
                <h4 class="text-success mb-2">1244397476@qq.com</h4>
                <div class="mb-2">
                     <router-link to="/contact/personName" class="btn btn-primary">Name</router-link>
                    <router-link :to="{name: 'phoneNumberLink'}" class="btn btn-primary">Phone Number</router-link>
                </div>
                <router-view></router-view>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'contact'
    }

</script>

<style scoped>

</style>



