// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
'use strict'
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import Home from './components/Home'
import Menu from './components/Menu'
import About from './components/about/About'
import Register from './components/Register'
import Login from './components/Login'

//二级路由
import Contact from './components/about/Contact'
import History from './components/about/History'
import OrderingGuide from './components/about/OrderingGuide'
import Delivery from './components/about/Delivery'

//三级路由
import PersonName from './components/about/contact/PersonName'
import PhoneNumber from './components/about/contact/PhoneNumber'

Vue.config.productionTip = false
Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'homeLink',
        components: {
            default: Home,

        }
    },
    {
        path: '/menu',
        name: 'menuLink',
        component: Menu,
        // beforeEnter: (to, from, next) => {
        //     alert('hello ');
        //     next();
        // }
    },
    {
        path: '/about',
        name: 'aboutLink',
        component: About,
        redirect: '/about/contact',
        children: [{
                path: '/about/contact',
                name: 'contactLink',
                component: Contact,
                redirect: '/contact/phoneNumber',
                children: [
                    { path: '/contact/personName', name: 'personNameLink', component: PersonName },
                    { path: '/contact/phoneNumber', name: 'phoneNumberLink', component: PhoneNumber }
                ]
            },
            { path: '/about/history', name: 'historyLink', component: History },
            { path: '/about/orderingGuide', name: 'orderingGuideLink', component: OrderingGuide },
            { path: '/about/delivery', name: 'deliveryLink', component: Delivery }
        ]
    },
    { path: '/register', name: 'registerLink', component: Register },
    { path: '/login', name: 'loginLink', component: Login }
]

const router = new VueRouter({
        routes,
        mode: 'history'
    })
    //全局守卫
    // router.beforeEach((to, from, next) => {
    //     if (to.path === '/login' || to.path === '/register') {
    //         next();
    //     } else {
    //         alert('还没有登录，请先登录！');
    //         next('/login');
    //     }
    // })

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>'
})


